import axios from 'axios'

class AuthService {
  async signIn( email, password ) {
    return axios({
      method: 'post',
      url: 'users/login',
      data: { email: email, password: password }
    })
  }
  async signUp(name, email, password) {
    return axios({
      method: 'post',
      url: 'users/register',
      data: { name, email, password }
    })
  }
}

export default new AuthService()